import allPage from "@/mixin/allPage_MX"
export default {
	mixins: [allPage],
	components: {
		
	},
	beforeCreate() {

	},
	data() {
		return {
			pageNo: 1,
			pageSize: 12,
			campaignList: [],
			list: [{
				name: '地铁测试',
				time: '0/2',
				time1: '0/2',
			},{
				name: '2018 Bmw Pitch',
				time: '0/2',
				time1: '0/2',
			},{
				name: 'ITTest2017',
				time: '0/2',
				time1: '0/2',
			}],
			triggered: false,
			updateLock: false,
		}
	},
	computed: {
		systemInfo() {
			return this.$store.state.userLogin.systemInfo
		},
	},
	onShow: function() {
		
	},
	onLoad: function(options) {
		this.getData();
		setTimeout(() => {
			this.triggered = true;
		}, 1000)
	},
	onReady: function() {
		
	},
	onHide: function(options) {
		
	},
	onReachBottom: function(options) {
		
	},

	onPullDownRefresh() {
		
	},
	mounted() {
		
	},
	watch: {
		
	},
	onPageScroll: function(e) {
		
	},
	methods: {
		getData(){
			if(!this.updateLock){
				this.updateLock = true;
			}else{
				return;
			}
			let parms = '},"campaignQuery":{"pageNo":' + this.pageNo + ',"pageSize":' + this.pageSize +'}';
			this.$store.dispatch('speed/getCampaigninfo', {parms,
				callback: (res) => {
					console.log(res);
					if (res.errorCode == 0) {
						let data = res.campaignResult.campaignInfos;
						this.campaignList.push(...data)
						if(data.length){
							this.updateLock = false;
						}
					}else{
						uni.showToast({
							title: res.errorMsg,
							icon: 'none',
							mask: true
						})
					}
					this.triggered = false;
					this.freshing = false;
					uni.hideLoading();
				},
			})
		},
		onRefresh() {
			if (this.freshing) return;
			this.updateLock = false;
			this.freshing = true;
			if (!this.triggered){//界面下拉触发，triggered可能不是true，要设为true  
				this.triggered = true;  
			};
			this.pageNo = 1;
			this.campaignList = [];
			this.getData();
		},
		onRestore() {
			this.triggered = 'restore'; // 需要重置
		},
		// 上拉加载
		updateRefresh() {
			this.pageNo++;
			this.getData();
		},
	},

}
